@import url(https://fonts.googleapis.com/css2?family=Mulish&display=swap);
/* GLOBAL */
*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

.contact_container{
    width: 100%;
    height: 80vh;
    background-color: #132743;
}

.heading{
    margin-top: 1rem;
    text-align: center;
    font-size: 2.5rem;
    font-weight: 700;
    color: #d7385e;
}

.form-label{
    font-size: 1rem;
    font-weight: 600;
    color: #d7385e;
}

footer{
    margin-top: 2rem;
    padding: 1rem 0rem;
    background-color: #132743;
    color: #d7385e;
    font-size: 1rem;
    font-weight: 600;
}

/* .gallery_div{
    width: 100%;
    height: 82vh;
} */

.about_btn{
    background-color: #edc988 !important;
    color: #132743 !important;
    font-size: 1rem !important;
    font-weight: 700 !important;
    transition: 0.5s !important;
}

.about_btn:hover{
    background-color: #d7385e !important;
}

/* FOR DESKTOP VIEW */

@media (min-width: 1024px){
    .nav-link{
        margin-left: 5rem;
    }
    .navlink_active{
        text-align: center;
        border-bottom: 0.1rem solid #d7385e;
    }
}

/* FOR IPAD / TABLET  */

/* @media (min-width: 769px) and (max-width: 1023px){

} */

/* FOR MOBILE VIEW */

@media (max-width: 768px){
    .nav-link{
        margin-left: 5rem;
    }
    .navlink_active{
        text-align: center;
        border-bottom: 0.1rem solid #d7385e;
    }
    #header{
        display: block;
        text-align: center;
    }
    #header figure img{
        padding-top: 30px;
        width: 100%;
        height: 100vh;
        margin-right: 50px;
    }
    .card_div{
        margin-left: 40px;
    }
    .gallery_container{
        margin-left: 6rem;
    }
    .flex_view h1{
        font-size: 2.5rem !important;
    }
    #header .brand_name{
        font-size: 2rem !important;
    }

    .about_btn{
        transition: 0.2s !important;
    }
    
    .about_btn:hover{
        background-color: #d7385e !important;
    }

    footer{
        width: 100%;
        height: auto;
    }
}

@media (max-width: 1023px){
    .nav-link{
        margin-left: 5rem;
    }
    .navlink_active{
        text-align: center;
        border-bottom: 0.1rem solid #d7385e;
    }
}
/* FOR Home Page And About Page Design */

#header{
    width: 100%;
    height: 83vh;
    background-color: #f8efd4;
    display: flex;
    align-items: center;
}

#header h1{
    font-size: 3rem;
    font-weight: 700;
    color: #132743;
}

#header .brand_name{
    color: #d7385e;
    font-size: 2.5rem;
}

#header h2{
    color: #132743;
    font-weight: 350;
}


.flex_view{
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.getting_start_btn{
    font-size: 1.6rem;
    font-weight: 500;
    padding: 1rem 1.5rem;
    border: 0.15rem solid #d7385e;
    border-radius: 5rem;
    letter-spacing: 0.1rem;
    display: inline-block;
    text-decoration: none;
    transition: 0.5s;
    color: #132743;
}

.getting_start_btn:hover{
    background-color: #132743;
    color: #d7385e;
}

#header figure img{
    width: 80%;
    height: 80%;
    float: right;
}

#header .animated{
    animation: up-down 2s ease-in-out infinite alternate-reverse both;
}
@-webkit-keyframes up-down{
    0%{
        transform: translateY(10px);
    }
    100%{
        transform: translateY(-10px);
    }
}
@keyframes up-down{
    0%{
        transform: translateY(10px);
    }
    100%{
        transform: translateY(-10px);
    }
}
/* For SERVICE PAGE */

.card_div{
    width: 100%;
    /* height: 100vh; */
    background-color: #fff;
    display: flex;
    flex-direction: column;
    /* align-items: center; */
}

.card{
    box-shadow: 0.2rem 0.2rem 0.3rem gray;
}

.card img{
    width: auto;
    height: 250px;
}

