}

.navlink_active{
    font-weight: bold;
    border-bottom: 0.1rem solid #d7385e;
}

.navbar-toggler{
    background-color: #d7385e !important;
    margin-right: 3rem;
}

span i{
    padding: 2px 2px;
    font-size: 2rem;
